.body {
  margin: 0;
  display: flex;
  flex-direction: column;
}
.topBody {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  margin-top: 70px;
}
.search {
  display: flex;
  padding: 10px 5px;
  width: 50%;
  font-size: 17px;
  border: 1px solid rgb(87, 59, 131);
  border-radius: 5px;
}
input:focus {
  outline: none;
}

.map {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  background-color: #efefef;
  min-height: 75vh;
}

.search-button {
  color: white;
  background-color: rgb(87, 59, 131);
  border: none;
  border-radius: 5px;
  font-size: 18px;
  margin-right: 2rem;
  padding: 0.5rem 1rem;
  margin-left: 10px;
  cursor: pointer;
}
* {
  text-decoration: none;
}
